import { FormControl, Validators } from '@angular/forms';
import { TextfieldComponent } from 'my-lib';
import { action } from '@storybook/addon-actions'; // สำหรับ Storybook v10
const meta = {
    title: 'MyLib/Textfield',
    component: TextfieldComponent,
    tags: ['autodocs'],
    argTypes: {
        valueChange: { action: 'valueChange' },
        size: {
            control: { type: 'select' },
            options: ['sm', 'md', 'lg'],
        },
        color: {
            control: { type: 'select' },
            options: ['primary', 'secondary', 'success', 'error'],
        },
    },
};
export default meta;
// helper: create FormControl
const withControl = (value, required = false) => {
    const validators = required
        ? [Validators.required, Validators.maxLength(10)]
        : [Validators.maxLength(10)];
    return new FormControl(value || '', validators);
};
// shared renderer with logging
const renderTemplate = (args) => {
    const valueAction = action('valueChange'); // ✅ สร้าง action instance
    const control = withControl(args.value, args.color === 'error');
    // subscribe valueChanges เพื่อส่งค่าไปที่ action
    control.valueChanges.subscribe((val) => {
        console.groupCollapsed(`Field Update: ${args.label || 'Field'}`);
        console.table({
            value: val,
            valid: control.valid,
            invalid: control.invalid,
            touched: control.touched,
            dirty: control.dirty,
            pristine: control.pristine,
            errors: control.errors,
        });
        console.groupEnd();
        valueAction(val); // ✅ ส่งค่าจริงไป Action
    });
    return {
        props: {
            ...args,
            control,
            valueChange: valueAction, // ✅ bind action ที่มีค่า
        },
        template: `
      <app-textfield
        [label]="label"
        [placeholder]="placeholder"
        [size]="size"
        [color]="color"
        [helperText]="helperText"
        [disabled]="disabled"
        [onlyNumbers]="onlyNumbers"
        [control]="control"
        (valueChange)="valueChange($event)"
      >
        <span class="material-symbols-outlined">clear</span>
      </app-textfield>
    `,
    };
};
// stories
export const Default = {
    render: renderTemplate,
    args: {
        label: 'Username',
        placeholder: 'Enter text',
        value: '',
        size: 'sm',
        color: 'primary',
        helperText: 'Max 10 characters',
        disabled: false,
        onlyNumbers: false,
    },
};
export const WithValue = {
    render: renderTemplate,
    args: {
        ...Default.args,
        value: 'Pre-filled text',
    },
};
export const ErrorState = {
    render: renderTemplate,
    args: {
        ...Default.args,
        value: '',
        color: 'error',
    },
};
// Size Stories
export const Small = {
    render: renderTemplate,
    args: { ...Default.args, size: 'sm' },
};
export const Medium = {
    render: renderTemplate,
    args: { ...Default.args, size: 'md' },
};
export const Large = {
    render: renderTemplate,
    args: { ...Default.args, size: 'lg' },
};
// Color Stories
export const Primary = {
    render: renderTemplate,
    args: { ...Default.args, color: 'primary' },
};
export const Secondary = {
    render: renderTemplate,
    args: { ...Default.args, color: 'secondary' },
};
export const Success = {
    render: renderTemplate,
    args: { ...Default.args, color: 'success' },
};
export const Error = {
    render: renderTemplate,
    args: { ...Default.args, color: 'error' },
};
